import wx from 'weixin-js-sdk';
import { giveReward } from "../../js/service/getData";

export const wxpay = (params) =>
    giveReward(params).then(function(res){
        let data = res.data.data;
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: data.appid, // 
            timestamp: data.timestamp, // 
            nonceStr: data.noncestr, // 
            signature: data.sign,// 
            jsApiList: [
                'chooseWXPay'
            ] // JS
        });
        wx.ready(function(){
            wx.chooseWXPay({
                timestamp: data.timestamp, // jssdktimestamptimeStampS
                nonceStr: data.noncestr, //  32 
                package: data.packageValue, // prepay_idprepay_id=***
                signType:data.signtype, // 'SHA1''MD5'
                paySign:data.sign, // 
                success: function (res) {
                    window.history.go(0);
                    // 
                },
                fail: function(res){
                    alert('');
                }
            });
        })
    })







