import wx from 'weixin-js-sdk';
import { share } from "../../js/service/getData";
// let src = window.location.href;
export const wxshare = (params,m) =>
    share(params).then(function(res){
        // console.log(res.data.data.appid);
        let data = res.data.data;
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: data.appid, // 
            timestamp: data.timestamp, // 
            nonceStr: data.noncestr, // 
            signature: data.sign,// 
            jsApiList: [
                'updateAppMessageShareData',
                'updateTimelineShareData',
            ] // JS
        });
        wx.ready(function(){
            //
            wx.updateAppMessageShareData({
                title: m.title, // 
                desc: m.desc, // 
                link: m.link, // 
                imgUrl: m.imgUrl, // 
                // type: '1', // ,musicvideolinklink
                // dataUrl: '1', // typemusicvideo
                success: function () {
                    // 
                },
                cancel: function () {
                    // 
                }
            });
            //
            wx.updateTimelineShareData({
                title: m.title, // 
                desc: m.desc, // 
                link: m.link, // 
                imgUrl: m.imgUrl, // 
                success: function () {
                    // 
                },
                cancel: function () {
                    // 
                }
            });
        })
    })







