
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {applyForm, applyPay, submitForm} from "../../../js/service/getData";
import func from "../../../js/handleFunction";
import wx from 'weixin-js-sdk';
export default {
components: {
Group,
GroupTitle,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton
},
data () {
return {
addressData: ChinaAddressData,
addressValue: ['', '', ''],
time1: '2017-06-01',

list:[],
numberValue: 0,
sendInfo:{
data:'',
file:{},
content:'',
},
info:{},
infoIndex:{},
content:'',
videoFile:{},
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
params:{
sign_type:2, //0 1 2
act_id:this.$route.query.act_id,//id
act_type:this.$route.query.activity_type,//
national:'',
province:'',
city:'',
country:'',
address:'',
langtitude:'',
latitude:'',
videoID:'',
photo:'',
music:'',
source:'pc',
info:{},//
friends:'',
permission:1,//1 2 3
content:'',//  
}
}
},
created() {
this.initData();
},
mounted(){

},
methods: {
async initData() {
let p = {"logintoken":this.logintoken,"loginuid":this.loginuid,"params":{"act_id":this.$route.query.act_id}};
let res = await applyForm(p);

res = res.data.data;

let arr = [];

for(let i = 0; i<res.length;i++){
if(res[i].type == 'select'){
for(let j = 0;j<res[i].extra_option.length;j++){
arr.push(res[i].extra_option[j].name);
}
}
}
this.list.push(arr);
this.info = res;
},

submitForm(){
this.params.info = this.infoIndex;
let params = {
loginuid:this.loginuid,
logintoken:this.logintoken,
params:this.params,
};
submitForm(params).then(res => {
if(res.data.err_msg == 'success'){

if(res.data.data.pay == 1){
this.applyPay(res.data.data);
}else{
alert('');
this.$router.go(-1);
}
}else{
alert(res.data.info)
}
})
},

applyPay(data){
let openid = localStorage.getItem('openid');
let p = {
loginuid:this.loginuid,
logintoken:this.logintoken,
params:{
pay_type:2,
pay_platform:1,
goodsSystem_id:data.goodsSystem_id,
order_no:data.order_no,
activity_id:data.activity_id,
openid:openid,
return_url:window.location.href,
}
};
applyPay(p).then(res => {
if(res.data.err_msg == 'success'){
let data = res.data.data;
wx.config({
debug: false, // ,apialertpclogpc
appId: data.appid, // 
timestamp: data.timestamp, // 
nonceStr: data.noncestr, // 
signature: data.sign,// 
jsApiList: [
'chooseWXPay'
] // JS
});
wx.ready(function(){
wx.chooseWXPay({
timestamp: data.timestamp, // jssdktimestamptimeStampS
nonceStr: data.noncestr, //  32 
package: data.packageValue, // prepay_idprepay_id=***
signType:data.signtype, // 'SHA1''MD5'
paySign:data.sign, // 
success: function (res) {
window.history.go(-1);

},
fail: function(res){
alert('');
}
});
})
}else{
alert('')
}

});
}
}
}



